/* Lamado en Gif.js */
.Gif{
  border: 1px solid #eee;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  margin: 16px;
  padding: 32px;
  width: 25%;
  height: 25%;
  color: white;
  text-decoration: none;
}
.Gif h4{
  margin: 0;
}

.Gif img{
  object-fit: contain;
}